.navbar {
  background-color: #0b0c0c;
  color: rgb(245, 19, 19);
  padding: 30px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.logo {
  font-size: 1.5rem;
}

.actions button {
  background-color: #220d8d;
  color: white;
  
  border: none;
  padding: 8px 16px;
  margin-left: 50px;
  cursor: pointer;
}

.actions button:hover {
  background-color: #e05712;
}

.actions div {
  display: flex;
  align-items: center;
}

.actions div button {
  margin-right: 20px;
}
.user-details {
  font-size: 1.5rem; /* Increase font size */
  padding: 20px; /* Add padding for better spacing */
  margin-top: 20px; /* Add margin to separate from the navbar */
}

.user-details p {
  margin-bottom: 10px; /* Add margin between each detail */
}
.logo {
  position: absolute; /* Position the logo */
  top: 4%; /* Move the logo to the vertical center */
  left: 50%; /* Move the logo to the horizontal center */
  transform: translate(-50%, -50%); /* Center the logo precisely */
}

.logo h1 {
  margin: 0; /* Remove default margin */
}
